const Users = require('../models/index').users;


exports.login = (data) => {
    return new Promise((resolve,reject) => {
        Users.find({ where : { username : data.username, password : data.password  } }).then(result => resolve(result)).catch(err => reject(err));
    });
};

exports.refresh = (data) => {
    return new Promise((resolve,reject) => {
        Users.find({ where : { username : data.username, password : data.password  } }).then(result => resolve(result)).catch(err => reject(err));
    });
};